$animation-triangle-right: triangle-right 40s linear infinite;
$animation-triangle-left: triangle-left 35s linear infinite;
$scale-5: scale(0.5,0.5);


html,body {
  margin: 0px;
  padding: 0px;
  width: 100%;
  height: 100%;
  font-size: 100;
  font-family: lato;
  cursor: url(../../assets/svg/cursor.svg), default; }

a {
  text-decoration: none;
  color: black; }

.a__cursor-red:hover {
  cursor: url(../../assets/svg/cursor-red.svg), default; }

p {
  margin: 0px;
  padding: 0px; }

h1 {
  margin: 0px;
  padding: 0px;
  font-size: 1.6rem; }

h2 {
  margin: 0px;
  padding: 0px;
  font-size: 1.2rem; }

h3 {
  margin: 0px;
  padding: 0px;
  font-size: 1rem; }




// Mobile first approach
.container {
  width: 100%;
  height: 100%; }

#preloader {
  width: 150px;
  height: 100%;
  position: absolute;
  top: 50px;
  left: 50%;
  transform: translateX(-50%); }

path#hair {
  stroke-dashoffset: 1523.38;
  stroke-dasharray: 1523.38;
  animation: hair 2s linear 1 forwards; }

path#eye {
  stroke-dashoffset: 1668.52;
  stroke-dasharray: 1668.52;
  animation: eye 2s linear 1 forwards; }

#rect {
  animation: slideIn 1s linear 1 forwards 3s; }

#child {
  width: 150px;
  height: 157px; }


#hero-section {
  width: 100%;
  height: auto;
  padding-top: 20px;
  color: white;
  background: linear-gradient(to right, #272A2C, #22AFDD);
  opacity: 0;

  .animation-area {
    list-style: none;
    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;

    li {
      opacity: 0;
      content: url("../../assets/svg/bg-triangle.svg");
      position: absolute; }

    li:nth-child(1) {
      top: 10%;
      left: 10%;
      animation: $animation-triangle-right;
      animation-delay: 25s; }

    li:nth-child(2) {
      top: 30%;
      left: 0%;
      animation: $animation-triangle-right;
      animation-delay: 10s;
      transform: $scale-5; }

    li:nth-child(3) {
      top: 90%;
      left: 10%;
      animation: $animation-triangle-right;
      animation-delay: 20s; }

    li:nth-child(4) {
      top: 70%;
      left: 20%;
      animation: $animation-triangle-right;
      animation-delay: 0s; }

    li:nth-child(5) {
      top: 20%;
      left: 70%;
      animation: $animation-triangle-left;
      animation-delay: 10s; }

    li:nth-child(6) {
      top: 40%;
      left: 85%;
      animation: $animation-triangle-left;
      animation-delay: 5s;
      transform: $scale-5; }

    li:nth-child(7) {
      top: 50%;
      left: 80%;
      animation: $animation-triangle-left;
      animation-delay: 20s; }

    li:nth-child(8) {
      top: 80%;
      left: 90%;
      animation: $animation-triangle-left;
      animation-delay: 0s;
      transform: $scale-5; }

    li:nth-child(9) {
      top: 0%;
      left: 90%; }

    li:nth-child(10) {
      top: 0%;
      left: 90%; }

    li:nth-child(11) {
      top: 0%;
      left: 90%; }

    li:nth-child(12) {
      top: 0%;
      left: 90%; } }


  navbar {
    float: right;
    margin-right: 20px;
    color: white;
    font-size: 12px;
    font-family: lato;
    opacity: 0;
    animation-delay: 4s;

    a {
      color: white;
      margin: 0px 10px; } }

  #together {
    margin-top: 50px;
    width: 100%;
    position: relative;

    #greeting {
      width: 45%;
      min-width: 156px;
      max-width: 240px;
      position: relative;
      margin: auto;
      font-family: chalkboard SE;
      opacity: 0;

      #chat-bubble {
        position: absolute;
        left: -65px;
        width: 90px;
        height: 60px;
        background-image: url(../../assets/svg/chat-bubble.svg);
        background-size: contain;
        background-repeat: no-repeat;

        text {
          font-size: 20px;
          position: relative;
          top: 13px;
          left: 20px; } }

      //chat-bubble
      img {
        width: 50%;
        position: relative;
        left: -65px;
        top: 25px; }

      p {
        padding-top: 50px;
        font-size: 18px; } } }

  #logo {
    width: 75px;
    height: 156px;
    overflow: hidden;
    position: absolute;
    top: 0px;
    right: 0px;
    opacity: 0;

    img {
      max-width: 150px;
      max-height: 157px; } }



  #explore {
    width: 50px;
    font-size: 14px;
    letter-spacing: 1.5px;
    margin: 20px auto 0px auto;
    opacity: 0;
    animation-delay: 5s;

    a {
      color: white; }

    svg {
      width: 100%;
      height: 50px;
      margin: 0px auto;
      stroke-dasharray: 120;
      stroke-dashoffset: 120;
      animation: zoosh 3.5s cubic-bezier(.85,.18,.52,.92) infinite; } } }

#how-it-works {
  width: 100%;
  height: auto;
  padding: 50px 0px;
  background-color: #F4FFFF;

  h1 {
    width: 160px;
    margin: 0px auto 50px auto;
    color: #048DD1;
    font-weight: bold; }

  #main-div {
    margin-bottom: 100px;
    display: flex;
    flex-direction: column;

    .box {
      width: 150px;
      height: 150px;
      border-radius: 8px;
      margin: 0px auto 0px auto;
      background-color: #e2e2e2;
      box-shadow: 1px 5px 6px 0px #bbbbbb;

      p {
        font-size: 12px;
        width: 100%;
        max-width: 180px;
        text-align: center;
        margin: 10px auto 30px auto;
        position: relative;
        top: 160px; } }

    img.arrow {
      width: 15px;
      margin: 100px auto 50px auto; } }

  #learn-more {
    width: 100px;
    height: 30px;
    background-color: #B50909;
    border-radius: 25px;
    margin: 0px auto;
    text-align: center;
    font-weight: 200;

    a {
      color: white;
      font-size: 14px;
      position: relative;
      top: 4px; } } }

#ebook {
  width: 100%;
  height: auto;
  padding: 20px 0px;
  background: linear-gradient(to bottom, #1293A5, #0EAFE1);
  text-align: center;

  h4 {
    color: #fff;
    text-align: center;
    font-weight: 300;

    span {
      color: #FFFD0A; } }

  input {
    width: 150px;
    height: 15px;
    margin: 5px 0px;
    border-radius: 5px;
    border: none;
    padding: 3px 10px;
    outline: none; }

  button {
    font-weight: 200;
    outline: none;
    color: #fff;
    width: 100px;
    height: 20px;
    margin-top: 10px;
    border: none;
    border-radius: 15px;
    background: linear-gradient(to right, #C36C17, #F3B63F); }

  label[name="spam-promise"] {
    font-size: 10px;
    color: #D4D4D4;
    font-weight: 200; } }

#hire-me {
  width: 100%;
  height: auto;

  #blue-thick-border {
    width: 90%;
    max-width: 550px;
    height: auto;
    border: 4px #18B3E0 solid;
    margin: 50px auto;

    a {
      color: black; }


    a:after {
      content: '';
      position: absolute;
      width: 50px;
      height: 30px;
      background-image: url(../../assets/svg/link-bg.svg);
      background-size: contain;
      background-repeat: no-repeat;
      transform: translateX(-45px);
      clip-path: inset(0px 0px 0px 0px);
      transition: clip-path 0.2s;
      z-index: -1; }

    a:hover:after {
      clip-path: inset(15px 0px 10px 0px); }


    #top {
      width: 100%;
      min-width: 250px;
      margin: 20px auto;

      h1 {
        letter-spacing: 5px;
        width: 145px;
        margin: 0px auto; }

      p {
        color: #5D5D5D;
        text-align: center; } }

    #bottom {
      display: flex;
      flex-direction: column;
      width: 80%;
      min-width: 250px;
      margin: 0px auto;
      text-align: center;

      .content {
        width: 100%;
        max-width: 200px;
        margin: 10px auto;
        font-size: 12px;

        h3 {
          margin-bottom: 5px; }

        .hidden-numbers {
          color: #939393;
          display: none;
          margin-right: 10px; } } } } }

#contact {
  width: 100%;
  height: auto;
  padding: 10px 0px;

  #shadow-box {
    width: 90%;
    min-width: 220px;
    max-width: 550px;
    height: auto;
    padding: 20px 0px;
    margin: 0px auto;
    box-shadow: 0px 20px 30px 0px #aaa;
    background: linear-gradient(to bottom, white 40%, #E6FFFE);
    z-index: 0;

    .footer--sidemargin {
      margin: 5px 20%; }


    #email-phone {
      margin-top: 20px;
      height: auto;

      p {
        letter-spacing: 2px;

        span.via {
          font-weight: 300;
          color: #444; }

        a {
          z-index: 0; } } }



    #social {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-top: 40px;

      div[name="connect"],div[name="links"] {
        width: 50%; } }

    .footer__navbar {
      font-size: 14px;
      width: 60%;
      max-width: 230px;
      margin: 50px auto 10px auto;
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      a {
        font-weight: 800;
        color: #808080;
        transition: color 0.5s; } }

    #copyrights {
      color: #949494;
      font-size: 10px;
      font-weight: 300;
      margin: 0px auto;
      text-align: center; } } }

@keyframes slideIn {
    from {
      transform: translateX(0px); }

    to {
      transform: translateX(450px); } }


@keyframes slideOut {
    from {
      transform: translateX(-450px); }

    to {
      transform: translateX(0px); } }



@keyframes hair {
  to {
    stroke-dashoffset: 0; } }

@keyframes hair-out {
  0% {
    stroke-dashoffset: 0; }

  100% {
    stroke-dashoffset: 1523.38; } }



@keyframes eye {
  0% {
    stroke-dashoffset: 1668.52; }

  100% {
    stroke-dashoffset: 3337.04; } }

@keyframes eye-out {
  0% {
    stroke-dashoffset: 3337.04; }

  100% {
    stroke-dashoffset: 1668.52; } }



.fadeInLeft {
  animation: fadeInLeft 1s cubic-bezier(.61,.39,.37,.87) 1 forwards 6s; }

.fadeInLeftShort {
  animation: fadeInLeftShort 1s cubic-bezier(.61,.39,.37,.87) 1 forwards 3s; }

.fadeInRight {
  animation: fadeInRight 1s cubic-bezier(.61,.39,.37,.87) 1 forwards 3s; }

.fadeInRightShort {
  animation: fadeInRightShort 1s cubic-bezier(.61,.39,.37,.87) 1 forwards 3s; }

.fadeInTopShort {
  animation: fadeInTopShort 1s cubic-bezier(.61,.39,.37,.87) 1 forwards 3s; }


@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translate3d(-20%, 0, 0); }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

@keyframes fadeInLeftShort {
  from {
    opacity: 0;
    transform: translate3d(-50px, 0, 0); }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translate3d(-20%, 0, 0); }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

@keyframes fadeInRightShort {
  from {
    opacity: 0;
    transform: translate3d(50px, 0, 0); }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

@keyframes fadeInTopShort {
  from {
    opacity: 0;
    transform: translateZ(-50px); }

  to {
    opacity: 1;
    transform: translateZ(0px); } }



@keyframes zoosh {
  50% {
    stroke-dashoffset: 0; }

  100% {
    stroke-dashoffset: -120; } }


@keyframes triangle-right {

  20% {
    opacity: 1; }

  90% {
    opacity: 1; }

  100% {
    opacity: 0;
    left: 70%;
    transform: rotate(500deg); } }


@keyframes triangle-left {

  10% {
    opacity: 1; }

  90% {
    opacity: 1; }

  100% {
    opacity: 0;
    left: 30%;
    transform: rotate(-500deg); } }





